.btn {
  padding: 1.04em 3.04em;
  display: inline-block;
  text-align: center;
  font-family: "GothamPro", sans-serif;
  font-size: 15px;
  line-height: 1;
  white-space: nowrap;
  transition: 0.3s;
}
.btn_secondary {
  color: #fbfbfb;
  background-image: linear-gradient(54deg, rgb(255, 157, 46) 0%, #f47213 98%);
  box-shadow: 0px 4px 24px 0px rgba(244, 114, 19, 0.3);
}
.btn_rounded {
  border-radius: 3em;
}
.btn_outline {
  color: #151d28;
  background-color: #fbfbfb;
  border: solid 1px #151d28;
}
.btn_circle {
  width: 24.4rem;
  height: 24.4rem;
  padding: 1.5em;
  font-size: 22px;
  font-weight: 400;
  color: #fbfbfb;
  background-image: linear-gradient(54deg, rgb(255, 157, 46) 0%, #f47213 98%);
  box-shadow: 0px 4px 24px 0px rgba(244, 114, 19, 0.3);
  border-radius: 50%;
}

@media (hover: hover) and (pointer: fine) {
  .btn_outline:hover {
    color: #fbfbfb;
    background-image: linear-gradient(93deg, rgb(0, 0, 255) 0%, rgb(184, 0, 255) 100%);
  }
  .btn_secondary:hover {
    background-color: #f38f23;
    background-image: none;
    color: #151d28;
    transition: none;
  }
  .btn_circle:hover {
    background: linear-gradient(54deg, #f47213 0%, #f47213 98%);
  }
}
