/* Header */
.header {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 100;
  transition: background-color 0.6s;
}
.header__inner {
  padding: 1.35rem 0;
  display: flex;
  align-items: center;
}
.header__right {
  display: flex;
  align-items: center;
  margin-left: auto;
  margin-right: calc(15px + 2rem + 2.8rem + 3rem);
}
.header-logo {
  padding: 0.4rem 1rem;
  max-width: 22rem;
  background: #fbfbfb;
  border-radius: 0.5rem;
}
.header__menu-trigger {
  position: absolute;
  right: calc(15px + 2rem);
  z-index: 1000;
}
.header.bg-painted {
  background-color: rgba(251, 251, 251, 0.5);
}
.header.bg-painted .header__contacts-link a {
  color: #151d28;
}
.header.bg-painted .header__contacts-link svg path {
  fill: #151d28;
  stroke: #151d28;
}
.header.bg-painted .menu-trigger__dash {
  background-color: #151d28;
}

.header-menu__logo, .header-menu-trigger, .header-menu-overl {
  display: none;
}

.contacts-link {
  display: flex;
  align-items: center;
  flex-wrap: nowrap;
  transition: 0.3s;
}
.contacts-link__icon {
  margin-right: 0.5em;
  transition: 0.3s;
}
.contacts-link__icon path {
  transition: inherit;
}
.contacts-link__icon--phone {
  width: 1.05em;
  height: 1.05em;
}
.contacts-link a {
  display: inline-flex;
  align-items: center;
  font-size: 20px;
  font-weight: 500;
  color: #7f7f7f;
  transition: color 0.3s;
}

.header-menu {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  transform: translateX(100%);
  z-index: 999;
  width: 75%;
  max-width: 160rem;
  background-color: #ebebeb;
  transition: transform ease-out 0.3s;
}
.header-menu__inner {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  height: 100%;
}
.header-menu__header {
  padding: 15px 30px;
  display: flex;
  align-items: center;
  flex: none;
  min-height: 8rem;
}
.header-menu__logo {
  display: block;
  width: 16rem;
  max-height: 3.2rem;
  flex: none;
  display: none;
}
.header-menu-scrollable {
  overflow-x: hidden;
  overflow-y: auto;
}
.header-menu__body {
  padding-left: 6rem;
  padding-right: 6rem;
  display: flex;
  flex-direction: column;
  flex: 1;
}
.header-menu__footer {
  padding-top: 15px;
  padding-bottom: 15px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  flex: none;
  min-height: 8rem;
}
.header-menu__footer .contacts-link {
  display: none;
}
.header-menu .js-menu-item {
  transform: translateX(5px);
  opacity: 0;
}
.header-menu.open {
  transform: translateX(0);
}
.header-menu.open .js-menu-item {
  transition: 500ms;
  transform: none;
  opacity: 1;
}
.header-menu-overl {
  display: block;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  height: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0);
  visibility: hidden;
  transition: 0.3s;
}
.header-menu-overl.open {
  z-index: 980;
  width: 100%;
  visibility: visible;
  background: rgba(0, 0, 0, 0.5);
}

.menu-trigger {
  display: block;
  width: 2.8rem;
  height: 2.1rem;
  position: relative;
  transition: 0.3s;
}
.menu-trigger__dash {
  position: absolute;
  display: block;
  width: 2.8rem;
  height: 0.3rem;
  background-color: #7f7f7f;
  transition: inherit;
}
.menu-trigger__dash:first-child {
  top: 0;
}
.menu-trigger__dash:nth-child(2) {
  top: 0.9rem;
}
.menu-trigger__dash:last-child {
  top: 1.8rem;
}
.menu-trigger.open {
  right: 17px;
}
.menu-trigger.open .menu-trigger__dash:first-child {
  transform-origin: 0px 2px;
  transform: rotate(45deg);
}
.menu-trigger.open .menu-trigger__dash:nth-child(2) {
  opacity: 0;
}
.menu-trigger.open .menu-trigger__dash:last-child {
  transform-origin: 2px 2px;
  transform: rotate(-45deg);
}

.header-menu__nav {
  display: flex;
  flex: 1;
}

.menu {
  flex: 1;
  position: relative;
}
.menu-item.lv1 {
  max-width: 36rem;
}
.menu-item > a {
  display: block;
  padding: 1rem 0;
  font-size: 37px;
  font-weight: 500;
  color: #151d28;
}
.menu-item.current-menu-item > a {
  color: #3200ff;
}

.sub-menu.lv2 {
  padding-top: 1rem;
  position: absolute;
  top: 0;
  left: 36rem;
  right: 0;
  bottom: 0;
  overflow: auto;
  opacity: 0;
  visibility: hidden;
  transition: 0.3s;
}

.menu-item.lv2 > a {
  padding: 0.6rem 0;
  font-size: 20px;
  font-weight: 400;
  transition: none;
}

.menu-item:hover > a {
  color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
  background-image: linear-gradient(94.72deg, #f38f23 -12.19%, #F36F0F 108.86%);
}
.menu-item-has-children:hover > .sub-menu {
  opacity: 1;
  visibility: visible;
}

@media (hover: hover) and (pointer: fine) {
  .menu-trigger:hover .menu-trigger__dash {
    background-color: #151d28;
  }
  .menu-trigger:not(.open):hover .menu-trigger__dash:first-child {
    transform: translateY(1px);
  }
  .menu-trigger:not(.open):hover .menu-trigger__dash:last-child {
    transform: translateY(-1px);
  }
  .menu-trigger.open:hover {
    transform: scale(0.92);
  }
  .contacts-link:hover a {
    color: #151d28;
  }
  .contacts-link:hover .contacts-link__icon path {
    fill: #151d28;
    stroke: #151d28;
  }
}
.menu-trigger:active .menu-trigger__dash {
  background-color: #151d28;
  transition: background-color 0;
}

.menu-trigger:not(.open):active .menu-trigger__dash:first-child {
  transform: translateY(1px);
}
.menu-trigger:not(.open):active .menu-trigger__dash:last-child {
  transform: translateY(-1px);
}

.menu-trigger.open:active {
  transform: scale(0.92);
}
.menu-trigger.open:active .menu-trigger__dash {
  transition: background-color 0;
}

.contacts-link:active a {
  color: #151d28;
  transition: none;
}
.contacts-link:active .contacts-link__icon path {
  fill: #151d28;
  stroke: #151d28;
  transition: none;
}

.menu-item:not(.current-menu-item):active > a {
  color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
  background-image: linear-gradient(94.72deg, #f38f23 -12.19%, #F36F0F 108.86%);
  transition: none;
}

