/* Advantages section */
.advantages {
  --item-content-max-width: 52rem;
}
.advantages__title {
  max-width: 80rem;
}
.advantages__box {
  position: relative;
  overflow: hidden;
}
.advantages__box-img {
  width: calc(100% - var(--item-content-max-width));
