  top: 0;
  left: 0;
  bottom: 0;
  z-index: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.advantages__box-img img {
  height: 90%;
}
.advantages__item {
  display: flex;
  justify-content: flex-end;
  padding: 5rem 2.5rem 5rem 0;
  min-height: 20rem;
  max-height: 24rem;
  position: relative;
  z-index: 1;
  border-radius: 20px;
  background-color: rgba(242, 243, 244, 0.85);
  overflow: hidden;
}
.advantages__item::after {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
  opacity: 0;
  transform: scale(0.9);
  border-radius: 2rem;
  background-image: linear-gradient(139deg, rgb(0, 102, 255) 0%, rgb(22, 78, 251) 31%, rgba(111, 15, 247, 0.77) 100%);
