}
.advantages__item-title h3::before {
  align-self: baseline;
  transform: translateY(-0.15em);
  flex: none;
  content: "";
  margin-right: 1em;
  display: inline-block;
  width: 8px;
  height: 8px;
  border-radius: 50%;
