/* Clients section */
.clients-slider {
  width: 100%;
  max-width: 1320px;
  height: 100%;
}
.clients-slider-wrapper {
  --swiper-navigation-size: 4.4rem;
  position: relative;
  height: 16rem;
}
.clients-slider-wrapper .swiper-button-prev,
.clients-slider-wrapper .swiper-button-next {
  width: calc(var(--swiper-navigation-size) * 0.68);
  height: calc(var(--swiper-navigation-size) * 0.68);
  color: #fbfbfb;
  background: #7f7f7f;
  border-radius: 50%;
  transition: 0.3s;
}
.clients-slider-wrapper .swiper-button-prev::after,
.clients-slider-wrapper .swiper-button-next::after {
  font-size: calc(var(--swiper-navigation-size) * 0.3);
}
.clients-slider-wrapper .swiper-button-prev {
  left: 3rem;
}
.clients-slider-wrapper .swiper-button-next {
  right: 3rem;
}
.clients-slide {
