    width: calc(100% - var(--item-content-max-width));
  }
  .advantages__item-title h3 {
    margin-bottom: 0.5em;
    font-size: 14px;
  }
  .advantages__item-descr {
    font-size: 10px;
  }
  /* Clients section */
  .clients-slider-wrapper {
    height: 30rem;
  }
  .clients-slider-wrapper .swiper-button-prev {
    left: 1.5rem;
  }
  .clients-slider-wrapper .swiper-button-next {
    right: 1.5rem;
  }
  .clients-slider {
    height: 100%;
  }
  .clients-slide {
    display: flex;
    justify-content: center;
    align-items: center;
    filter: grayscale(1);
    transition: filter 0.3s;
  }
  /* Presentation section */
  .presentation__item {
    width: 100%;
    min-width: auto;
    flex: none;
  }
  .presentation__item-badge {
    font-size: 14px;
  }
  .presentation__item-icon {
    width: 4rem;
    height: 4rem;
  }
  .presentation__item-title {
    font-size: 18px;
  }
  /* Proposal section */
  .proposal-form-wrap {
    padding-bottom: unset;
  }
  .proposal-form__submit-wrap {
    position: static;
  }
  .submit-btn-align-right-xs {
    text-align: right;
  }
  .d-block_xs {
    display: block;
  }
  .d-inline_xs {
    display: inline;
  }
  .d-none_xs {
    display: none;
  }
}
